.idea-tile {
  width: 400px;
  height: 150px;
  margin: 10px;
  background: white;
  box-sizing: border-box;
  padding: 5px;
  box-shadow: 7px 4px 7px -2px rgba(0, 0, 0, 0.15);
  border-radius: 2px;
  position: relative; }
  .idea-tile__title {
    font-weight: bold;
    font-size: 14px;
    width: 90%; }
  .idea-tile__description {
    font-size: 12px;
    width: 90%; }
  .idea-tile__date {
    font-size: 10px;
    color: grey;
    position: absolute;
    bottom: 10px;
    right: 10px; }
  .idea-tile__delete-btn {
    border: none;
    margin: 0;
    padding: 0;
    width: auto;
    overflow: visible;
    background: transparent;
    line-height: normal;
    -webkit-appearance: none;
    cursor: pointer;
    position: absolute;
    display: none;
    top: 5px;
    right: 10px;
    font-size: medium;
    color: #f76b8a; }
  .idea-tile:hover .idea-tile__delete-btn {
    display: block; }
  .idea-tile:hover:focus-within .idea-tile__delete-btn {
    display: none; }
  .idea-tile textarea {
    border: none;
    resize: none; }
    .idea-tile textarea:focus {
      border: 1px solid lightgrey; }

.textarea__counter {
  color: #ff570c;
  font-size: 9px;
  background: white; }

textarea + .textarea__counter {
  display: none; }

textarea:focus + .textarea__counter {
  display: block; }

.loader-ellipsis {
  display: block;
  position: relative;
  width: 64px;
  height: 64px;
  margin: 20px auto; }

.loader-ellipsis div {
  position: absolute;
  top: 27px;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background: #fff;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0); }

.loader-ellipsis div:nth-child(1) {
  left: 6px;
  -webkit-animation: loader-ellipsis1 0.6s infinite;
          animation: loader-ellipsis1 0.6s infinite; }

.loader-ellipsis div:nth-child(2) {
  left: 6px;
  -webkit-animation: loader-ellipsis2 0.6s infinite;
          animation: loader-ellipsis2 0.6s infinite; }

.loader-ellipsis div:nth-child(3) {
  left: 26px;
  -webkit-animation: loader-ellipsis2 0.6s infinite;
          animation: loader-ellipsis2 0.6s infinite; }

.loader-ellipsis div:nth-child(4) {
  left: 45px;
  -webkit-animation: loader-ellipsis3 0.6s infinite;
          animation: loader-ellipsis3 0.6s infinite; }

@-webkit-keyframes loader-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes loader-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@-webkit-keyframes loader-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

@keyframes loader-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

@-webkit-keyframes loader-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0); } }

@keyframes loader-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0); } }

.idea-container .idea-menu {
  margin: 20px 0; }

.idea-container .idea-sort {
  display: inline-block;
  flex-wrap: wrap;
  vertical-align: bottom;
  margin: 0 8px; }
  .idea-container .idea-sort__input {
    position: relative;
    background-color: white;
    border: 1px solid #ced4da;
    width: 200px;
    font-size: 14px;
    padding: 4px;
    border-radius: 3px; }

.idea-container .idea-add {
  display: inline-block;
  background: #f76b8a;
  margin: 0 8px;
  line-height: normal;
  color: white;
  font-weight: bold;
  font-size: 14px;
  padding: 10px;
  border-radius: 3px;
  cursor: pointer; }

.idea-container .idea-list {
  background: #ded5c4;
  display: flex;
  flex-wrap: wrap; }

.notification {
  position: fixed;
  top: 10px;
  right: 10px;
  padding: 15px;
  width: 250px;
  border-radius: 6px;
  color: #1f441f;
  background: #d0ffdd;
  font-size: 12px; }

.header {
  background-color: #111f4d;
  min-height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 700;
  color: white; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
